.portfolionavigator{
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.portfolionavigator ul{
    display: block;
    padding-left: 10px;
}

.portfolionavigator li{
   display: inline;
}

.portfolionavigator li button{
    background-color: #262a4f;
    color: white;
    border: solid 2px black;
    padding: 10px 10px;
    font-size:larger;
}

.portfolionavigator li button:hover{
    background-color:#111;
    cursor: pointer;
}

.portfoliopiece h1{
    text-align: center;
}

.portfoliopiece h3{
    text-align: center;
    margin-top: -15px;
}

.portfoliopiece p{
    text-align: center;
}

.portfoliopiece{
    border: solid 2px black;
    border-radius: 10px;
    background-color: #262a4f5c;
    color: white;
    width: 1500px;
    text-align: left;
    padding: 25px;
    margin: 10px auto;
}

.screenshot{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 1200px;
}

.portfoliopiece iframe{
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media (min-width: 320px) and (max-width: 374px){
    .steam{
        width: 270px;
    }

    .steamdiv{
        display: block;
        margin-left: -15px;
        margin-right: auto;
    }

    .portfoliopiece{
        width: 75vw;
    }

    .portfolionavigator{
        font-size:smaller;
    }

    .screenshotphone{
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }

    .screenshot{
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    .youtube{
        width: 100%;
        height: 100%;
    }

    h3{
        font-size: smaller;
    }
}

