.NavigationBar{
    text-align: center;
    width: 100vw;
}

.NavigationBar ul {
    list-style-type: none;
    margin-top: 0;
    overflow: hidden;

    display: block;
    
    background-color: #262a4f;

    border-bottom: solid 2px black;
}

.NavigationBar li{
    
    display: inline-block;
    border-right: 1px solid #bbb;
}

.NavigationBar li:last-child{
    border-right: none;
}

.NavigationBar a{
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

.NavigationBar li a:hover{
    
    background-color: #111;
}

@media (min-width: 320px) and (max-width: 374px){
    .NavigationBar ul {
        margin-left: -50px;
    }
}

@media (min-width: 375px) and (max-width: 424px){
    .NavigationBar ul {
        margin-left: -50px;
    }
}

@media (min-width: 425px) and (max-width: 767px){
    .NavigationBar ul {
        margin-left: -50px;
    }
}

@media (min-width: 768px) and (max-width: 1023px){
    .NavigationBar ul {
        margin-left: -50px;
        font-size: larger;
    }
}

@media (min-width: 1024px) and (max-width: 1439px){
    .NavigationBar ul {
        margin-left: -50px;
        font-size: larger;
    }
}

@media (min-width: 1440px) and (max-width: 2559px){
    .NavigationBar ul {
        margin-left: -50px;
        font-size: x-large;
    }
}

@media (min-width: 2560px){
    .NavigationBar ul {
        font-size:xx-large;
        margin-left: -50px;
    }
}