.profilepic{
    border-color: black;
    border-width: 5px;
    border-style: solid;

    width: 600px;
    border-radius: 1000px;
    
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.firstInfo{
    margin-top: -15px;
}

h1{
    text-align: center;
}

.separator{
    margin-top: 10px;
    height: 2px;
    background: black;
}

.mail{
    margin-top: -20px;
    text-align: center;
}

.mail a{
    font-size: larger;
    color:cadetblue;
}

.mail a:hover{
    color: white;
}

.cvbutton{
    margin-left: auto;
    margin-right: auto;
    margin-top: 25px;
    display: block;

    height: 50px;

    font-size: large;

    background-color: #262a4f;
    color: white;
    border: solid 2px black;
}

.cvbutton:hover{
    background-color:#111;
    cursor: pointer;
}

.socials{
    text-align: center;
    margin-top: 10px;
}

.linkedin{
    margin-left: 25px;
    position: relative;
    top: -16px;
}

.githubstats iframe{
    border: none;
}

@media (min-width: 320px) and (max-width: 374px){
    .profilepic{
        width: 100%;
    }

    h1{
        font-size: x-large;
    }

    .mail{
        font-size: small;
    }

    .githubstats{
        margin-top: -60px;
        margin-left: -100px;
        margin-right: auto;
        scale: 70%;
        text-align: center;
        display: block;
        
    }

    .githubdisclaimer{
        margin-top: -50px;
        font-size: small;
        text-align: center;
    }
}

