html {
  background: radial-gradient(ellipse, #262a4f 0%, #0d0d22 100%);
  min-height: 100vh;  
  min-height: -webkit-fill-available;
}

a{
  color:#57a2b4;
}

.starContainer {
  position: fixed;
  animation: background-stars-animation 100s linear infinite;
}

.star {
  position: fixed;
  z-index: -1;
  border-radius: 50%;
  box-shadow: 90px 0px #fff;
}

.bio{
  border: solid 2px black;
  border-radius: 25px;
  border-top-right-radius: 50px;
  border-top-left-radius: 50px;
  background-color: #262a4f5c;
  color: white;
  width: 400px;
  text-align: center;
  padding: 25px;
  margin: 0 auto;
  opacity: 0;
  animation: fade-in-animation 2s;
  animation-fill-mode: forwards;
}

.small-logo{
  position: absolute;
  opacity: 0;
  width: 50px;
  height: 50px;
  animation: fade-in-animation linear 4s;
  animation-fill-mode: forwards;
}

.info{
  border: solid 2px black;
  border-radius: 10px;
  background-color: #262a4f5c;
  color: white;
  width: 80vw;
  text-align: left;
  padding: 25px;
  margin: 10px auto;
}

@keyframes fade-in-animation{
  from{
    opacity: 0;
  }

  to{
    opacity: 100;
  }
}

@keyframes background-stars-animation {
  from {
      transform: translate3d(0px, 0px, 0px);
  }

  to {
      transform: translate3d(1600px, 1600px, 0px);
  }
}

.icons{
  text-align: center;
  display: block;
}

.icons ul{
  list-style: none;
  margin-left: -80px;
}

.icons li{
  display: inline;
  padding: 35px;
}

.spotify{
  border-radius: 12px;
  margin: 100px;
  width: auto;
}

.man{
  margin-left: 400px;
}

@media (min-width: 320px) and (max-width: 374px){
  .info{
    width: 75vw;
  }

  .bio{
    width: 75vw;
    padding-bottom: 0vw;
  }

  .bio h1{
    font-size:x-large;
  }

  .icons{
    margin-left: 30px;
    margin-right: auto;
    /* width: 200px; */
  }

  .icons li img{
    width: 12vw;
    height: 12vw;
  }

  .icons li{
    padding-left: 5px;
  }

  .man{
    width: 50vw;
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .spotify{
    width: 0px;
    position: fixed;  
  }
}

